import React from 'react'
import { Link, Outlet } from 'react-router-dom'
import 'bootstrap/dist/js/bootstrap.min.js'
import 'bootstrap/dist/css/bootstrap.min.css'
export default function AdminNav({name}) {
  return (
            <>
<div className="row">
        <div className="col-12">
            <div className="navbar navbar-expand-md">
                <div className="navbar-brand">
                    E - commerce
                </div>
                <div className="navbar-toggler">
                    <div className="navbar-toggler-icon" data-bs-toggle="collapse" data-bs-target="#admin1"></div>
                </div>
                <div className="collapse navbar-collapse" id='admin1'>

                <div className="navbar-nav ms-auto">
                    <Link to='/admin/addproduct'>
                    
                    <button className="navbar-item me-2 btn btn-outline-success">Add product</button>
                    </Link>
                    
                    
                 <Link to='/admin/orders'>
                 <button className="navbar-item me-2 btn btn-outline-success">orders</button>
                 </Link>
                    <Link to='/admin/feedback'>
                    <button className="navbar-item me-2 btn btn-outline-success">Feed back</button>
                    </Link>
                    <Link>
                    <button className="navbar-item me-2 btn btn-outline-success">New Product</button>
                    </Link>
                     
                 <Link to='/E-commerce/'>
                 <button className="navbar-item me-2 btn btn-outline-success">My site</button>
                 </Link>
                </div>
                </div>
            </div>
        </div>
</div>
        
        </>
  )
}
